}

.loader {
  fill: hsl(350deg, 80%, 52%);
  width: 100%;
  height: 100%;
  display: grid;
  align-items: center;
  justify-content: center;
  z-index: 5;
}
.loader svg {
  animation: rotationRight 1500ms infinite linear;
  width: 4rem;
  height: 4rem;
}

.error-message {
  transform: translateY(20rem);
}

