/*Nav Content*/
.dropdown-content {
  position: absolute;
  top: calc(100% - 2rem);
  background: #fff;
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
  z-index: 0;
  padding-top: 1rem;
  border-radius: 3px;
  transition: 250ms all ease-in-out;
  box-shadow: 0.3rem 0.3rem 0.5rem 0 rgba(0, 0, 0, 0.2);
}
@media only screen and (min-width: 68.75em) {
  .dropdown-content {
    opacity: 1;
    position: static;
    pointer-events: auto;
    visibility: visible;
    box-shadow: none;
    border-radius: 0;
    align-items: start !important;
    width: 100%;
  }
}
.dropdown-content.active {
  opacity: 1;
  top: 100%;
  pointer-events: auto;
  visibility: visible;
}

.drop-down > .dropdown-content > a {
  display: inline-block;
  padding: 0.5rem 5rem 0.5rem 2rem;
  width: 100%;
  transition: background 500ms ease-in-out;
}
@media only screen and (min-width: 68.75em) {
  .drop-down > .dropdown-content > a {
    padding-left: 4rem;
  }
}
.drop-down > .dropdown-content > a:hover {
  background: #DADADA;
}
.drop-down > .dropdown-content > a.active {
  background: #DADADA;
}

.pagination {
  margin: 2rem 0;
  padding-inline: 3rem;
  width: 100%;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(2rem, 1fr));
  gap: 1.5rem;
}
@media only screen and (min-width: 68.75em) {
  .pagination {
    width: 30%;
    position: fixed;
    right: 5%;
    bottom: 5%;
  }
}
.pagination__btn--next, .pagination__btn--prev {
  display: flex;
  align-items: center;
  border: none;
  outline: none;
  -webkit-user-select: none;
     -moz-user-select: none;
          user-select: none;
  cursor: pointer;
  padding: 0.8rem 1.5rem;
  background: #000;
  border-radius: 100px;
}
@media only screen and (min-width: 68.75em) {
  .pagination__btn--next, .pagination__btn--prev {
    border: 0.5px solid #fff;
  }
}
.pagination__btn--next > svg, .pagination__btn--prev > svg {
  transition: all 200ms ease-in-out;
  fill: currentColor;
}
.pagination__btn--next {
  justify-self: end;
}
.pagination__btn--next > svg {
  margin-left: 0.5rem;
}
.pagination__btn--next:hover > svg, .pagination__btn--next:focus > svg {
  margin-left: 1rem;
}
.pagination__btn--prev {
  justify-self: start;
}
.pagination__btn--prev > svg {
  margin-right: 0.5rem;
}
.pagination__btn--prev:hover > svg, .pagination__btn--prev:focus > svg {
  margin-right: 1rem;
}

.search-container {
